export function fileType(file){
    switch(file){
        case 'txt':
            return require('@/assets/icon/txt文件.png')
        case 'maf':
            return require('@/assets/icon/编码方案.png')
        case 'xlsx':
            return require('@/assets/icon/XLS.png')
        case 'xls':
            return require('@/assets/icon/XLS.png')
        case 'png':
            return require('@/assets/icon/png.png')
        case 'jpg':
            return require('@/assets/icon/png.png')
        case 'vcf':
            return require('@/assets/icon/vcf.png')
        case 'docx':
            return require('@/assets/icon/docx.png')
        case 'md':
            return require('@/assets/icon/markdown.png')
        default :
            return require('@/assets/icon/文件.png')
    }
    
}