export function unitConversion(limit){
    var size = "";
    if(limit < 0.1 * 1024){                            //小于0.1KB，则转化成B
        size = limit.toFixed(2) + "B"
    }else if(limit < 0.1 * 1024 * 1024){            //小于0.1MB，则转化成KB
        size = (limit/1024).toFixed(2) + "KB"
    }else if(limit < 0.1 * 1024 * 1024 * 1024){        //小于0.1GB，则转化成MB
        size = (limit/(1024 * 1024)).toFixed(2) + "MB"
    }else{                                            //其他转化成GB
        size = (limit/(1024 * 1024 * 1024)).toFixed(2) + "GB"
    }

    var sizeStr = size + "";                        //转成字符串
    var index = sizeStr.indexOf(".");                    //获取小数点处的索引
    var dou = sizeStr.substr(index + 1 ,2)            //获取小数点后两位的值
    if(dou == "00"){                                //判断后两位是否为00，如果是则删除00                
        return sizeStr.substring(0, index) + sizeStr.substr(index + 3, 2)
    }
    return size;
}
import { Loading } from 'element-ui'

var loading

export function startLoading() {
    loading = Loading.service({
        lock: true,
        text: '上传中....',
        background: 'rgba(0, 0, 0, 0.7)'
    })
}

export function endLoading() {
    loading.close()
}
//传入非图片的文件，提供文件类型对应的图片渲染页面
export function getfileImgs(file){
    switch (file) {
        case 'text/plain':
            return require('@/assets/icon/txt.png')
        case 'application/msword':
            return require('@/assets/icon/docx.png')
        case 'application/vnd.openxmlformats-officedocument.wordprocessingml.document':
            return require('@/assets/icon/docx.png')
        case 'application/vnd.ms-excel':
            return require('@/assets/icon/XLS.png')
        case 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet':
            return require('@/assets/icon/XLS.png')
        case 'application/pdf':
            return require('@/assets/icon/Pdf.png')
        case 'application/vnd.openxmlformats-officedocument.presentationml.presentation':
            return require('@/assets/icon/ppt.png')
        case 'application/vnd.ms-powerpoint':
            return require('@/assets/icon/ppt.png')
        default:
            return require('@/assets/icon/文件.png')
    }
}
export function fileType(){
    return [
    'text/plain',
    'application/msword',
    'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
    'application/vnd.ms-excel',
    'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    'application/pdf',
    'application/vnd.openxmlformats-officedocument.presentationml.presentation',
    'application/vnd.ms-powerpoint'
]
}